}
.as_toggle{
  display: none;
}
.as_heading{
  font-size: 33px;
  text-transform: capitalize;
  padding-bottom: 17px;
  position: relative;
  margin: -8px 0 0;
  color: var(--dark-color1);
  font-weight: 600;
}
.form-control{
  height: 45px;
  background-color: #ffffff;
  border: 1px solid rgb(121 121 121 / 20%);
  outline: none;
  box-shadow: none !important;
  color: var(--primary-color);
  padding: 0 28px;
  border-radius: 45px;
}
.form-control:focus{
  border-color: var(--secondary-color) !important;
}
.form-control::placeholder{
  color: #797979;
}
.as_subheading{
  font-size: 22px;
  text-transform: capitalize;
  color: var(--dark-color1);
}
.as_orange{
  color: var(--secondary-color);
}
.as_white{
  color: var(--white-color);
}


.as_btn,.as_btn:focus,.as_btn:visited{
  height: 45px;
  background-image: -moz-linear-gradient( 0deg, rgb(244,170,54) 0%, rgb(243,115,53) 100%);
  background-image: -webkit-linear-gradient( 0deg, rgb(244,170,54) 0%, rgb(243,115,53) 100%);
  background-image: -ms-linear-gradient( 0deg, rgb(244,170,54) 0%, rgb(243,115,53) 100%);
  font-size: 14px;
  text-transform: capitalize;
  display: inline-flex;
  align-items: center;
  padding: 0 23px;
  border: none !important;
  position: relative;
  color: var(--white-color);
  border-radius: 45px;
  outline: none;
  min-width: 160px;
  justify-content: center;
  outline: none;
  z-index:1;
  overflow:hidden;
}
.as_btn:after{
  position:absolute;
  z-index:-1;
  content:'';
  top:0;
  bottom:0;
  left:100%;
  right:0;
  background-image: -moz-linear-gradient( 0deg, rgb(244,170,54) 0%, rgb(243,115,53) 100%);
  background-image: -webkit-linear-gradient( 0deg, rgb(244,170,54) 0%, rgb(243,115,53) 100%);
  background-image: -ms-linear-gradient( 0deg, rgb(244,170,54) 0%, rgb(243,115,53) 100%);
}
.as_btn:hover:after{
  left:0
}
.as_btn:hover {
  color: var(--white-color) !important;
}
.as_verticle_center{
  display: flex;
  align-items: center;
}
.slick-dots {
  padding: 0;
  margin: 30px 0 0 0;
  list-style: none;
}
.slick-dots li {
  display: inline-flex;
}
.slick-dots li button {
  display: none;
}
.slick-dots li {
  height: 9px;
  width: 9px;
  background-color: #234a64;
  border-radius: 100%;
  margin: 0 6px;
  cursor: pointer;
}
.slick-dots li.slick-active {
  background-color: var(--secondary-color);
}

.slick-slide{
  outline: none;
}
.only-timepicker .datepicker--nav,
.only-timepicker .datepicker--content {
  display: none;
}
.only-timepicker .datepicker--time {
  border-top: none;
}
.datepicker {
  background-color: #ffffff;
  border: 1px solid rgb(255 255 255 / 10%);
  color: var(--primary-color);
}
.datepicker--nav {
  border-bottom: 1px solid rgb(255 255 255 / 10%);
}
.datepicker--nav>div {
  background-color: transparent !important;
  color: var(--primary-color);
}
.datepicker--pointer {
  background-color: #ffffff;
  border-top-color: rgb(255 255 255 / 10%);
  border-right-color: rgb(255 255 255 / 10%);
}
.datepicker--cell:hover{
  background-color: var(--secondary-color);
  color: var(--white-color) !important;
}
.datepicker--cell.-current-,.datepicker--day-name {
  color: var(--secondary-color);
}


body{
  font-size: 16px;
  line-height: 26px;
  margin: 0;
  background-color: #ffffff;
  color: var(--primary-color);
  font-family: 'Lato', sans-serif;
  overflow-x: hidden;
  font-weight: 500;
}

/* header  */
.as_header_wrapper {
  background-color: rgb(17 16 17 / 60%);
  /* position: fixed; */
  top:0;
  left: 0;
  right: 0;
  z-index: 9;
}
.as_menu ul {
  padding: 0;
  margin: 0;
  list-style: none;
}
.as_menu >ul >li {
  display: inline-flex;
  text-transform: capitalize;
  margin: 0 30px;
  color: #ffffff;
  position: relative;
}
.as_info_detail:last-child{
  margin-right: 0px;
}
.as_header_detail {
  width: calc(100% - 449px);
}
.as_menu > ul > li > a {
  padding: 23px 0;
  position: relative;
  transition: all 0.3s linear;
}
.as_wishlist{
  margin-left: 20px;
}
.as_menu >ul >li > a:before{
  content: '';
  position: absolute;
  width: 0;
  height: 3px;
  background-color: var(--secondary-color);
  top: 0%;
  left: 50%;
  transform: translateX(-50%);
  -webkit-transform: translateX(-50%);
  -moz-transform: translateX(-50%);
}
.as_menu > ul > li > a:hover:before,.as_menu > ul > li > a.active:before{
  width: 29px;
}
.as_menu ul li a:hover,.as_menu ul li a.active{
  color: var(--secondary-color);
}
.as_info_detail {
  border-bottom: 1px solid rgb(255 255 255 / 10%);
  padding: 3px 0;
}
.as_search_wrapper {
  max-width: 415px;
  width: 100%;
  position: relative;
}
.as_search_wrapper>a{
  position:absolute;
  top:11px;
  right:20px;
}
.as_search_wrapper .form-control{
  height: 45px;
  border: 1px solid #fff;
  border-radius: 45px;
  background-color: transparent;
  color: #ffffff;
}
.as_search_wrapper .form-control::placeholder{
  color: #ffffff;
}
.as_user {
  position: relative;
  margin-left: 30px;
}
.as_search {
  cursor: pointer;
}
.as_info_detail ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
.as_info_detail ul li {
  display: inline-flex;
  padding: 0 15px;
}

.as_info_detail ul li a,.as_info_detail ul li a:focus{
  text-decoration: none;
  color: #ffffff;
}
.as_infobox{
  display: flex;
  color: #ffffff;
}
.as_infobox:hover{
  color: var(--secondary-color);
}
.as_info_detail ul li .as_infobox>span {
  margin-right: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.as_right_info {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.as_logo {
  display: flex;
  color: rgb(233, 140, 18);
}
.as_cart_wrapper {
  margin-left:30px;
  position: relative;
}
.as_cart_wrapper>span {
  position: relative;
  cursor: pointer;
  margin-right:20px;
}
.as_cart_wrapper>span .as_cartnumber {
  width: 18px;
  height: 18px;
  background-color: var(--secondary-color);
  border-radius: 100%;
  font-size: 9px;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  right: -12px;
  color: #ffffff;
}
.as_cart {
  position: relative;
}
.as_cart_box {
  position: absolute;
  top: 100%;
  right: 0;
  width: 300px;
  background-color: #ffffff;
  z-index: 1;
  padding: 5px 20px 20px;
  box-shadow: 0 20px 20px rgb(255 255 255 / 5%);
  border-radius: 20px;
  margin-top: 20px;
  opacity: 0;
  visibility: hidden;
  box-shadow: -2px 0px 28px 0px rgb(0 0 0 / 5%);
  transition: all 0.3s linear;
}
.as_cart.cart_open .as_cart_box {
  opacity: 1;
  visibility: visible;
  margin-top: 10px;
}
.as_cart_list .as_cart_img{
float:left;
}
.as_cart_list .as_cart_img img{
width:70px;
  height: 70px;
  object-fit: cover;
}
.as_cart_info{
width:calc(100% - 70px);
float:right;
padding-left:10px;
}
.as_cart_list .as_cart_info a{
font-size:16px;
font-weight:500;
font-family:'Philosopher', sans-serif;
margin-bottom:5px;
width:110px;
text-transform:capitalize;
color: var(--dark-color1);
}
.as_cart_list {
  border-bottom: 1px solid rgb(255 255 255 / 10%);
  max-height: 250px;
  overflow-x: hidden;
  overflow-y: auto;
}
.as_cart_list ul{
  margin:0;
  padding: 0 0 20px;
  list-style:none;
  border-bottom: 1px solid #e7e7e7;
}
.as_cart_list li{
width: 100%;
display: flex;
align-items: center;
margin-top: 20px;
}
.as_cart_list .as_cart_info p{
font-size:14px;
}
.as_cart_box .as_cart_total , .as_cart_box .as_cart_btn{
padding-right:20px;
}
.as_cart_box .as_cart_total p{
margin-top:10px;
text-align:left;
text-transform:capitalize;
}
.as_cart_box .as_cart_total p span{
float:right;
font-family:'Philosopher', sans-serif;
}
.as_cart_box .as_cart_btn{
text-align:center;
padding-top:5px;
display: flex;
}
.as_cart_box .as_cart_btn button{
background-color: var(--dark-color1);
padding:0 5px;
text-transform:capitalize;
min-width: 120px;
margin-right: 1;
}
.as_cart_box .as_cart_btn button::after{
  border-left-color: var(--dark-color1);
}
.as_cart_box .as_cart_btn button::before{
  border-right-color: var(--dark-color1);
}
.as_cart_box .as_cart_btn button:hover{
background-color:var(--secondary-color);
}
.as_cart_box .as_cart_btn button:hover::after{
  border-left-color: var(--secondary-color);
}
.as_cart_box .as_cart_btn button:hover::before{
  border-right-color: var(--secondary-color);
}
.as_cart_box .as_cart_btn button:first-child{
  margin-right: 15px;
}
.as_cart_list::-webkit-scrollbar {
width:4px;
}
/* .as_cart_list::-webkit-scrollbar-track {
-webkit-box-shadow:inset 0 0 6px rgba(0,0,0,.3);
} */
.as_cart_list::-webkit-scrollbar-thumb {
background-color:#ff6f00;
outline:#708090 solid 1px;
}

.as_menu >ul >li .as_submenu{
  position: absolute;
  left: 0;
  top: 100%;
  background-color: var(--dark-color2);
  padding: 15px 0;
  border-radius: 10px;
  width: 270px;
  z-index: 999;
  opacity: 0;
  visibility: hidden;
}
.as_menu >ul >li >.as_submenu{
  transform: translateY(-10px);
}
.as_menu >ul >li:hover >.as_submenu{
  transform: translateY(0);
  opacity: 1;
  visibility: visible;
}
.as_menu >ul >li .as_submenu li a {
  padding: 3px 13px;
  display: flex;
}

.as_menu >ul >li .as_submenu>li>.as_submenu {
  right: 100%;
  top: 8px;
  left: auto;
  transform: translateX(-20px);
}
.as_menu >ul >li .as_submenu>li:hover>.as_submenu{
  transform: translateX(-10px);
  opacity: 1;
  visibility: visible;
}

.as_menu >ul >li .as_submenu>li {
  position: relative;
}

.as_menu >ul >li .as_submenu:before {
  content: '';
  position: absolute;
  border-bottom: 7px solid var(--dark-color2);
  border-left: 5px solid transparent;
  border-right: 5px solid transparent;
  bottom:100%;
  left:13px;
}
.as_menu >ul >li > .as_submenu > li >.as_submenu:before {
  bottom:auto;
  top:10px;
  left:100%;
  border-bottom: 5px solid transparent;
  border-top: 5px solid transparent;
  border-left:  7px solid var(--dark-color2);
}

